* {
  box-sizing: border-box;
  color: #3f3e41;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

.sidebar {
  z-index: 200;
  opacity: 0;
  visibility: hidden;
  background-color: #fff;
  flex-direction: column;
  gap: .5rem;
  width: 45%;
  height: 100vh;
  padding: 2rem .5rem;
  transition: all .3s;
  display: flex;
  position: fixed;
  transform: translateX(-100%);
  box-shadow: 0 4px 8px #0000001a;
}

.sidebar-active {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

header {
  z-index: 100;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 12%;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.fa.fa-bars {
  display: none;
}

.navigation {
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
  display: flex;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.sublogo {
  color: #6b2da8;
}

nav {
  justify-content: flex-start;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

nav a {
  border-bottom: 2px solid #0000;
  padding: .5rem 0;
  font-weight: 550;
  transition: all .3s;
}

nav a:hover {
  color: #6b2da8;
  border-bottom: 2px solid #6b2da8;
}

.user-register {
  gap: 1rem;
  font-weight: 550;
  display: flex;
}

.login-btn, .get-help-btn {
  background-color: #0000;
  border-radius: .5rem;
  padding: .5rem;
  transition: all .3s;
}

.fa-bell {
  transition: all .3s;
}

.fa-bell:hover {
  color: #6b2da8;
}

.login-btn:hover, .get-help-btn:hover {
  background-color: #e2dfe3;
}

section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  height: 100vh;
  padding: 5rem 12%;
  display: flex;
}

.hidden {
  display: none;
}

.blurred {
  filter: blur(5px);
}

.welcome-container {
  border-bottom: 1px solid #727272;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 20%;
  display: flex;
}

.manage-account-btn {
  color: #fff;
  background: linear-gradient(45deg, #6b2da8, #53129e9d);
  border-radius: .5rem;
  padding: .5rem 1rem;
  font-weight: 700;
  transition: all .3s;
}

.manage-account-btn:hover {
  transform: scale(1.05);
}

.fa-gear {
  color: #fff;
  padding-right: .5rem;
}

.features-container {
  justify-content: center;
  align-items: center;
  gap: 2rem;
  width: 100%;
  height: 80%;
  display: flex;
}

.feature1, .feature2 {
  background: #f0eff1;
  border-radius: .8rem;
  padding: 1rem;
  transition: all .3s;
  box-shadow: 0 4px 8px #0000002e;
}

.feature1:hover, .feature2:hover {
  transform: scale(1.02);
}

.feature1 {
  justify-content: space-between;
  display: flex;
}

.feature1 img, .feature2 img {
  background-color: #0000;
}

.feature1-details-text h2, .feature1-details-text p {
  color: #fff;
  background: none;
}

.feature1-details-text p {
  font-size: .8rem;
}

.feature1-details-text h2 {
  font-weight: 800;
}

.feature1-details-text {
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.feature1-details {
  background: none;
  flex-direction: column;
  justify-content: space-between;
  gap: .5rem;
  width: 60%;
  display: flex;
}

.feature-id-validation {
  background-image: linear-gradient(to top left, #39b385, #8ed74d);
}

.feature-gate-pass {
  background-image: linear-gradient(to top left, #e52a5a, #ff585f);
}

.feature-log-tracker {
  background-image: linear-gradient(to top left, #1640e8, #2667fe);
}

.get-started {
  color: #6b2da8;
  background-color: #f1f1f1;
  border: 2px solid #0000;
  border-radius: .5rem;
  width: 50%;
  padding: .5rem 1rem;
  font-weight: 600;
  transition: all .3s;
}

.column1 {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 50%;
  height: 100%;
  display: flex;
}

.feature-id-validation, .feature-gate-pass {
  width: 100%;
  height: 50%;
}

.feature-log-tracker {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  display: flex;
}

.feature-log-tracker.get-started {
  align-self: center;
}

.feature-log-tracker img {
  width: 50%;
}

@media (width <= 480px) {
  section {
    height: auto;
  }

  .fa.fa-bars {
    color: #3f3e41;
    display: flex;
  }

  header {
    z-index: 100;
    background-color: #fff;
    border-bottom: .5px solid #afafaf;
    width: 100%;
    padding: 1rem;
  }

  .navigation {
    justify-content: center;
    width: 100%;
    display: flex;
  }

  .navigation nav, .user-register {
    display: none;
  }

  .features-container {
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    display: flex;
  }

  .column1 {
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    height: 100%;
    display: flex;
  }

  .feature1 {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: fit-content;
    display: flex;
  }

  .feature1-details {
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: 100%;
    display: flex;
  }

  .feature1 img, .feature2 img {
    width: 50%;
  }

  .welcome-container {
    padding-bottom: 1rem;
  }

  .welcome-details h1 {
    font-size: 1.1rem;
  }

  .welcome-details p {
    font-size: .9rem;
  }

  .manage-account-btn {
    font-size: .7rem;
  }

  .fa-gear {
    display: none;
  }

  .get-started {
    width: auto;
  }

  .feature2 {
    width: 100%;
  }

  .feature2.get-started {
    width: 4rem;
  }

  .sidebar-container {
    flex-direction: column;
    display: flex;
  }

  .sidebar-btns {
    border-radius: .5rem;
    gap: 1rem;
    width: 100%;
    padding: 1rem .5rem;
    font-size: 1.1rem;
    font-weight: 700;
    transition: all .3s;
    display: flex;
  }

  .sidebar-btns:hover, .sidebar-btns:hover i, .sidebar-btns:hover p {
    background-color: #e2dfe3;
  }

  .sidebar-btns i {
    color: #6b2da8;
    background-color: #fff;
    transition: all .3s;
  }

  .sidebar-btns p {
    transition: all .3s;
  }

  .sidebar-btns a {
    gap: .6rem;
    width: 100%;
    display: flex;
  }

  nav a:hover {
    color: #6b2da8;
    border-bottom: none;
  }
}
